@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400;1,500;1,600;1,700&display=swap");

//variables

$contrast-color: black;

//Genereal styles
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
}
h1 {
  font-size: 2.5rem;
  text-transform: uppercase;
}
h2 {
  font-size: 1.5rem;
}
.header {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 20px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  display: flex;
  justify-content: center;

  .container-header {
    width: 80%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    nav {
      ul {
        list-style: none;
        display: flex;
        flex-direction: row;
        gap: 10px;
        li::before {
          content: "|";
          padding-left: 10px;
          padding-right: 10px;
        }
        li {
          a {
            text-decoration: none;
            color: black;
          }
        }
      }
    }
    .search-input {
      position: relative;
      input {
        padding: 8px 8px 8px 25px;
        border-radius: 5px;
        border-width: 1px;
        border-color: lightgray;
      }
      input:hover {
        box-shadow: 0 7px 14px $contrast-color, 0 3px 6px transparent;
        cursor: pointer;
      }
      img {
        width: 20px;
        position: absolute;
        top: 8px;
        left: 4px;
      }
    }
  }
}

.header-logo {
  width: 150px;
  height: auto;
  line-height: 0px;
}

.image-player-court {
  width: 60px;
  height: auto;
  border: 1px solid black;
  border-radius: 50%;
}
.player-one {
  position: absolute;
  top: 0;
  left: 0;
}

/*Swiper*/
.swiper {
  .swiper-slide {
    background: black;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: white;
    padding: 0 0 20px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 500px;
    .div-content-image {
      width: 80%;
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-top: 300px;
    }
    p {
      font-size: 0.8rem;
    }
    h2,
    p,
    .button-read-more {
      background: black;
      padding: 8px 20px;
      color: white;
    }
    h2,
    p {
      width: fit-content;
      position: relative;
    }
    .button-read-more {
      width: 200px;
      font-size: 0.8rem;
    }
    h2:before,
    p:before {
      content: "";
      display: block;
      width: 4px;
      height: 55%;
      left: 10px;
      top: 9px;
      background: red;
      position: absolute;
    }
  }
}

.container {
  padding: 20px;
}

.player-card {
  width: 300px;
  height: 430px;
  margin: 10px;
  background-color: rgba(255, 255, 255, 0.639);
  border-radius: 0.5rem;
  box-shadow: 0.05rem 0.1rem 0.3rem -0.03rem rgba(0, 0, 0, 0.45);
  display: flex;
  gap: 20px;
  flex-direction: column;

  .player-card-information {
    display: flex;
    gap: 15px;
    height: 100%;
    flex-direction: column;
    padding: 0 20px;
    text-align: justify;
    justify-content: center;
    p {
      font-size: 0.8rem;
    }
  }
  img {
    width: 100%;
    border-radius: 0.5rem;
    height: auto;
  }
  button {
    width: 50%;
    color: white;
    background-color: #265290;
    border-radius: 6px;
    padding: 4px 0;
  }
}
.cards {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: hidden;
  overflow-y: hidden;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  list-style: none;
}
.cover {
  padding: 0px 30px;
  position: relative;
}
.cards::-webkit-scrollbar {
  width: 5px;
  height: 8px;
  background-color: #aaa;
}

.cards::-webkit-scrollbar-thumb {
  display: none;
}
.cards::-webkit-scrollbar {
  -webkit-appearance: none;
}

.right {
  position: absolute;
  transform: rotate(-45deg);
  border: solid black;
  border-width: 3px 0 0 3px;
  display: inline-block;
  padding: 3px;
  left: 0;
  top: 50%;
  //transform: translateY(-50%);
}
.left {
  position: absolute;
  transform: rotate(135deg);
  border: solid black;
  border-width: 3px 0 0 3px;
  display: inline-block;
  padding: 3px;
  right: 0;
  top: 50%;
  cursor: pointer;
  //transform: translateY(-50%);
}

/*Jugadoras*/

.article-competition-flag-country {
  width: 20px;
  height: auto;
}
.article-player-flag-country {
  width: 40px;
  height: auto;
}
.links-information {
  flex-direction: column;
}

/*Portada*/

.welcome-container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  //background: rgb(78, 195, 255);

  .welcome-contents {
    background-color: white;
    width: 70%;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    .welcome-information {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 52px;
      flex-direction: column;
    }
    p {
      text-align: center;
    }
    h1 {
      text-shadow: 2px 1px #000;
      color: #265290;
      font-size: 6rem;
    }

    .link-index-page {
      display: flex;
      justify-content: center;
    }

    ul {
      display: flex;
      justify-content: center;
      list-style: none;
      gap: 20px;

      li {
        a {
          font-size: 24px;
          text-decoration: none;
        }
      }
    }
  }
}

