.title-presentacion {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.ball-right {
  width: 40px;
  height: 84px;
  cursor: pointer;
  animation: right 2s;

  @keyframes right {
    0% {
      transform: translateX(0) rotate(0deg);
    }
    50% {
      transform: translateX(100px) rotate(360deg);
    }
    100% {
      transform: translateX(0) rotate(0deg);
    }
  }
}

.ball {
  width: 40px;
  height: 84px;
  cursor: pointer;

  &:hover {
    animation: rotation 2s infinite linear;

    @keyframes rotation {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
  }
}
